import { useState, useEffect } from 'react';
import { styled, keyframes } from 'styled-components';
import bgImg from '../../../images/resource/images/.jpeg';

function Subject() {
	const [animate, setAnimate] = useState(false);
	useEffect(() => {
		const handleScroll = () => {
			// :      
			const midScreen = window.innerHeight / 1.3;

			if (window.scrollY > midScreen) {
				setAnimate(true);
			} else {
				setAnimate(false);
			}
		};

		window.addEventListener('scroll', handleScroll);

		return () => {
			//      
			window.removeEventListener('scroll', handleScroll);
		};
	}, []);
	return (
		<SubjectContainer>
			<TitleWrapper>
				<div className={animate ? 'innerWrapper animate' : ''}>
					<h2>01</h2>
					<p>  </p>
					<h3>
						<strong></strong> 
					</h3>
				</div>
			</TitleWrapper>
			<StepWrapper>
				<div className={animate ? 'innerWrapper animate' : ''}>
					<h3>01</h3>
					<p>
						  
						<br />
						  
					</p>
					<p>  </p>
				</div>
				<div className={animate ? 'innerWrapper animate' : ''}>
					<h3>02</h3>
					<p>
						  
						<br />
						    
					</p>
					<p>   </p>
				</div>
				<div className={animate ? 'innerWrapper animate' : ''}>
					<h3>03</h3>
					<p>
						    
						<br />
						    
					</p>
					<p> </p>
				</div>
			</StepWrapper>
		</SubjectContainer>
	);
}

const slideDown = keyframes`
    from {
        transform: translate(0, -50%);
        opacity: 0;
    }
    to {
        transform: translate(0,0);
        opacity: 1;
    }
`;

const slideUp = keyframes`
    from {
        transform: translate(0, 50%);
        opacity: 0;
    }
    to {
        transform: translate(0,0);
        opacity: 1;
    }
`;

const SubjectContainer = styled.section`
	width: 100%;
	height: 92vh;
	background-image: url(${bgImg});
	background-repeat: no-repeat;
	background-size: cover;
	display: flex;
	flex-direction: column;
	justify-content: space-around;

	@media screen and (max-width: 400px) {
		padding: 30px 0;
	}

	* {
		margin: 0;
	}
`;

const TitleWrapper = styled.div`
	width: 100%;
	height: 50%;
	color: #fff;

	.innerWrapper {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		h2 {
			font-size: 6rem;
			@media screen and (max-width: 690px) {
				font-size: 4rem;
			}
		}
		h3 {
			font-size: 3rem;
			font-weight: lighter;
			@media screen and (max-width: 690px) {
				font-size: 2rem;
			}
		}
		h3 strong {
			border-bottom: 5px solid currentColor;
			text-decoration: none; /*    */
			padding-bottom: 3px; /* 3px     */
		}

		p {
			font-size: 1.5rem;
			font-weight: normal;
		}
	}

	.animate {
		animation: ${slideDown} 4s ease;
	}
`;

const StepWrapper = styled.div`
	width: 100%;
	height: 50;
	display: flex;
	justify-content: space-around;
	color: #fff;

	@media screen and (max-width: 600px) {
		flex-direction: column;
	}

	.innerWrapper {
		display: flex;
		flex-direction: column;
		align-items: center;
		h3 {
			font-size: 3rem;
			@media screen and (max-width: 600px) {
				font-size: 2rem;
			}
		}
		p {
			text-align: center;
			margin-bottom: 20px;
			@media screen and (max-width: 600px) {
				font-size: 0.9rem;
			}
		}
	}
	.animate {
		animation: ${slideUp} 4s ease;
	}
`;

export default Subject;
