import { useState, useEffect } from 'react';
import { styled, keyframes } from 'styled-components';
import profileImg from '../../../images/resource/images/.png';
import univLogo from '../../../images/resource/images/.png';

function Introduce() {
	const [animate, setAnimate] = useState(false);
	useEffect(() => {
		const handleScroll = () => {
			// :      
			const midScreen = window.innerHeight / 2;

			if (window.scrollY > midScreen) {
				setAnimate(true);
			} else {
				setAnimate(false);
			}
		};

		window.addEventListener('scroll', handleScroll);

		return () => {
			//      
			window.removeEventListener('scroll', handleScroll);
		};
	}, []);
	return (
		<IntroduceContainer>
			<ImageWrapper>
				<img
					className={animate ? 'animate' : ''}
					src={profileImg}
					alt="profile img"
				/>
			</ImageWrapper>
			<TextWrapper>
				<div className={animate ? 'titleWrapper animate' : 'titleWrapper'}>
					<h2>.</h2>
					<h2>
						<strong>
							SKY 
							<br />  .
						</strong>
					</h2>
				</div>
				<div className={animate ? 'contentWrapper animate' : 'contentWrapper'}>
					<p>
						24  ,    
						<br />
						      .
					</p>
					<ul>
						<li>   </li>
						<li>     ()</li>
						<li>    ()</li>
						<li>   </li>
						<li>  </li>
						<li>  </li>
						<li>  </li>
						<li>  </li>
						<li>)  </li>
					</ul>
				</div>
			</TextWrapper>
		</IntroduceContainer>
	);
}

const slideRight = keyframes`
    from {
        transform: translate(30%, 0);
        opacity: 0;
    }
    to {
        transform: translate(0,0);
        opacity: 1;
    }
`;

const slideLeft = keyframes`
    from {
        transform: translate(-30%, 0);
        opacity: 0;
    }
    to {
        transform: translate(0, 0);
        opacity: 1;
    }
`;

const IntroduceContainer = styled.section`
	width: 100%;
	height: 92vh;
	position: relative;
	display: flex;
	justify-content: space-between;
	background: url(${univLogo}) center no-repeat;
	background-size: 40%;

	@media screen and (max-width: 690px) {
		background-size: 90%;
		align-items: center;
	}

	* {
		margin: 0;
	}
`;

const TextWrapper = styled.div`
	padding: 10vh 15vw;
	.animate {
		animation: ${slideLeft} 4s ease;
	}

	.titleWrapper {
		h2 {
			font-size: 2rem;
			font-weight: normal;

			@media screen and (max-width: 690px) {
				font-size: 1.5rem;
			}
		}
	}
	.contentWrapper {
		p {
			margin: 35px 0;

			@media screen and (max-width: 690px) {
				font-size: 0.9rem;
			}
		}
		ul {
			li {
				list-style-type: none;
				position: relative;
				right: 35px;
				margin-bottom: 10px;

				@media screen and (max-width: 690px) {
					font-size: 0.9rem;
				}

				&:before {
					content: '';
					display: inline-block;
					width: 3px;
					height: 9px;
					background: #3d81b8;
					transform: rotate(45deg);
					margin-right: 10px;
					vertical-align: super;
				}
			}
		}
	}
`;

const ImageWrapper = styled.div`
	width: 45%;
	height: 100%;
	position: absolute;
	right: 10vw;
	bottom: 0;
	display: flex;
	align-items: flex-end;

	.animate {
		animation: ${slideRight} 4s ease;
	}

	@media screen and (max-width: 690px) {
		display: none;
	}

	img {
		width: 100%;
		height: auto;
	}
`;

export default Introduce;
